 .App {
	 text-align: center;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 font-family: "Open Sans", sans-serif;
	 padding: 40px;
}
 .App-logo {
	 animation: App-logo-spin infinite 20s linear;
	 height: 40vmin;
	 pointer-events: none;
}
 .App-header {
	 background-color: #282c34;
	 min-height: 100vh;
	 display: flex;
	 flex-direction: column;
	 align-items: center;
	 justify-content: center;
	 font-size: calc(10px + 2vmin);
	 color: white;
}
 .App-link {
	 color: #61dafb;
}
 @keyframes App-logo-spin {
	 from {
		 transform: rotate(0deg);
	}
	 to {
		 transform: rotate(360deg);
	}
}
 .login {
	 width: 27em;
	 height: 40em;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 margin-top: 5px;
	 position: relative;
	 z-index: 99;
}
 .login .container {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 background: #1C2237;
	 
	 box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
	 border-radius: 4px;
	 position: relative;
	 z-index: 99;
	 width: 100%;
	 height: 100%;
	 z-index: 99;
	 padding: 17px 10px;
}
 .login .right-side {
	 display: flex;
	 flex-direction: column;
	 justify-content: center;
	 height: 90%;
	 background-color: #b82424;
	 background: linear-gradient(120deg, rgba(233, 62, 58, 1) 0%, rgba(235, 78, 59, 1) 30%, rgba(240, 125, 62, 1) 60%, rgba(246, 160, 48, 1) 90%, rgba(251, 186, 24, 1) 100%);
	 width: 100%;
	 position: absolute;
	 right: -34%;
	 border-radius: 6px;
	 z-index: 1;
	 transition: all 400ms ease-in-out;
	 cursor: pointer;
	 box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);
}
 .login .right-side.right {
	 right: -40%;
	 align-items: flex-end;
}
 .login .right-side.right:hover {
	 right: -45%;
}
 .login .right-side.left {
	 right: 40%;
	 align-items: flex-start;
}
 .login .right-side.left:hover {
	 right: 45%;
}
 .login .right-side .text {
	 font-size: 21px;
	 font-weight: 500;
	 color: #fff;
	 margin-right: 3em;
	 margin-left: 3em;
}
 